import {Registry} from 'cs-web-components-base';
import {prefixNS} from './helpers';
import ChecklistDetails from './ChecklistDetails';
import ChecklistItemDetails from './ChecklistItemDetails';
import ChecklistItemStatusCellRenderer from './ChecklistItemStatusCellRenderer';

Registry.registerComponent(prefixNS('ChecklistDetails'), ChecklistDetails);
Registry.registerComponent(prefixNS('ChecklistItemDetails'), ChecklistItemDetails);
Registry.registerComponent(
    prefixNS('ChecklistItemStatusCellRenderer'), ChecklistItemStatusCellRenderer
);

module.exports = [// eslint-disable-line no-undef
    ChecklistDetails, ChecklistItemDetails, ChecklistItemStatusCellRenderer
];



